import React from "react";
import Service from "../data/Service";
import Cookie from 'cookie';
import {Form, Icon, Input, Button, Modal, message} from "antd";
const FormItem = Form.Item;

class Login extends React.Component{


    constructor(props){
        super(props);
        this.state={
            forgetPwdVisible:false,
            resetPwdVisible:false,
            phone:"",
            password:"",
            code:"",
        }
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                Service().loginUser(values.username,values.password).then((data)=>{
                    if(data == "success"){
                        document.cookie = Cookie.serialize('user',values.username);
                        message.success("");
                        this.props.form.resetFields();//
                        document.location.href="/";//

                    }else{
                        message.error("");
                    }
                }).catch((err)=>{
                    console.log(err);
                })
            }
        });
    };
    //
    forgetPwd = () => {
        this.setState({
            phone:"",
            password:"",
            code:"",
            resetPwdVisible:false,
            forgetPwdVisible:true
        });
    };
    //
    backForgetPwdModal = () => {
        this.setState({resetPwdVisible:false})
    };
    //
    getPhone = (e) => {
        this.setState({phone:e.target.value});
    };
    //
    getIdentifyCode = () => {
        if(this.state.phone === ""){
            message.error("")
        }else if(!this.state.phone.match(/^1[34578]\d{9}$/)){
            message.error("")
        }else{
            message.success("");
            this.setState({resetPwdVisible:true})
        }
    };
    //
    getPwd = (e) => {
        this.setState({password:e.target.value});
    };
    //
    getICode = (e) => {
        this.setState({code:e.target.value});
    };
    //
    resetPassword = (e) => {
        if(this.state.password === ""){
            message.error("");
        }else if(this.state.code === ""){
            message.error("");
        }else{
            message.success("");
            this.setState({forgetPwdVisible:false});
        }
    };
    disabledPwd = () => {
        const pwd=document.getElementById("pwd");
        pwd.type='password'
    };
    showPwd = () => {
        const pwd=document.getElementById("pwd");
        pwd.type='text'
    };
    render(){
        const suffix=<i className="iconfont icon-kanguo" onMouseOver={this.showPwd} onMouseOut={this.disabledPwd}></i>
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        };
        const {getFieldDecorator} = this.props.form;
        return <div className="Login">
            <Form onSubmit={this.handleSubmit} className="login-form">
                <FormItem
                    {...formItemLayout}
                    label=""
                >
                    {getFieldDecorator('username',{
                        rules:[{required:true,message:""}]
                    })(
                        <Input prefix={<Icon type='user'/>} placeholder=""/>
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label=""
                >
                    {getFieldDecorator('password',{
                        rules:[{required:true,message:""}]
                    })(
                        <Input prefix={<Icon type='lock' />} placeholder="" type="password"/>
                    )}
                </FormItem>
                <FormItem
                    {...tailFormItemLayout}
                >
                    <a className="login-form-forgot" href="javascript:;" onClick={this.forgetPwd}></a>
                    <Button type="primary" htmlType="submit" className="login-form-button"></Button>
                </FormItem>
            </Form>
            <Modal
                className="login-resetPwd-modal"
                width={350}
                visible={this.state.forgetPwdVisible}
                footer={null}
                onCancel={()=>{this.setState({forgetPwdVisible:false})}}
                title=""
            >
                {this.state.forgetPwdVisible && <div>
                    {this.state.resetPwdVisible && <Icon type="arrow-left" onClick={this.backForgetPwdModal}/>}
                    {!this.state.resetPwdVisible && <div className="login-forgetPwd-modal-phone">
                        <p></p>
                        <Input placeholder="" value={this.state.phone} onChange={this.getPhone}/>
                        <Button onClick={this.getIdentifyCode} type="primary"></Button>
                    </div>}
                    {this.state.resetPwdVisible && <div className="login-forgetPwd-modal-form">
                        <p></p>
                        <Form>
                            <FormItem>
                                <Input value={this.state.phone} disabled={true} />
                            </FormItem>
                            <FormItem>
                                <Input id="pwd" type="password" value={this.state.password} placeholder="" onChange={this.getPwd} suffix={suffix}/>
                            </FormItem>
                            <FormItem>
                                <Input placeholder="" value={this.state.code} onChange={this.getICode}/>
                            </FormItem>
                            <FormItem>
                                <Button type="primary" onClick={this.resetPassword}></Button>
                            </FormItem>
                        </Form>
                    </div>}
                </div>}
            </Modal>
        </div>
    }
}
export default Form.create()(Login);


