import React from "react";
import "./index.less";
import {Button, Layout, Menu, Icon} from "antd";
import {Link} from "react-router-dom";
import Cookie from 'cookie';
import Home from "../Home";
import logo from "../assets/logo.png";
const { Header, Footer, Content} = Layout;


// ReactDOM.render(<Home/>, document.getElementById("root"));

class Index extends React.Component{
    constructor(props){
        super(props);
        this.state={
            id:props.id || 0,
        }
    }
    render(){
        const {children} = this.props;
        const { user } = Cookie.parse(document.cookie);
        return <div className="Index">
            <Layout>
                <Header>
                    <div className="logo">
                        <Link to="/"><img src={logo}/></Link>
                    </div>
                    <div className="nav-menu">
                        <Menu mode="horizontal">
                            <Menu.Item key="chuan">
                                
                            </Menu.Item>
                            <Menu.Item key="lu">
                                
                            </Menu.Item>
                            <Menu.Item key="yue">
                                
                            </Menu.Item>
                            <Menu.Item key="su">
                                
                            </Menu.Item>
                            <Menu.Item key="zhe">
                                
                            </Menu.Item>
                            <Menu.Item key="min">
                                
                            </Menu.Item>
                            <Menu.Item key="xiang">
                                
                            </Menu.Item>
                            <Menu.Item key="hui">
                                
                            </Menu.Item>
                        </Menu>
                    </div>
                    { user == undefined || user == "" && <div className="index-header-right">
                        <Link to="/login"><Button></Button></Link>
                        <Link to="/register"><Button></Button></Link>

                        {/*<Link to="/test"></Link>*/}
                    </div>}
                    {user && user != "undefined" && user !== "" && <div className="index-header-right">
                        <Link to="/logout"><Button></Button></Link>
                    </div>}
                </Header>
                <Content style={{padding: "0"}}>
                    {children}
                </Content>
            </Layout>
        </div>
    }
}
export default Index;




