import React from "react";
import Service from "../data/Service";
import {Form, Icon, Button, Input, message, Checkbox, Modal} from "antd";
const FormItem = Form.Item;

class Register extends React.Component{
    constructor(props){
        super(props);
        this.state={
            confirmDirty:false,
            agreementVisible:false,

        }
    }
    checkPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
            callback('');
        } else {
            callback();
        }
    };
    checkConfirm = (rule, value, callback) => {
        const form = this.props.form;
        if (value && this.state.confirmDirty) {
            form.validateFields(['confirm'], { force: true });
        }
        callback();
    };
    handleConfirmBlur = (e) => {
        const value = e.target.value;
        this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    };

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                if(values.agreement === undefined){
                    message.error("")
                }else{
                    Service().registerUser(values.username,values.password).then((data)=>{
                        if(data != undefined){
                            //
                            message.success(",");
                            this.props.form.resetFields();//
                            window.location.href = "/#/login";
                        }else{
                            message.error("")
                        }
                    }).catch(function (error) {
                        console.log(error);
                    })
                }
            }
        });
    };

    showAgreement = () => {
        this.setState({agreementVisible:true})
    };

    render(){
        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        };
        const {getFieldDecorator}=this.props.form;
        return <div className="Register">
            <Form className="register-form" onSubmit={this.handleSubmit}>
                <FormItem
                    {...formItemLayout}
                    label=""
                >
                    {getFieldDecorator('phone',{
                        rules:[{
                            pattern: /^1[34578]\d{9}$/, message: ''},
                            {required: true , message : ''}
                            ]
                    })(
                        <Input prefix={<Icon type="mail" />} placeholder=""/>
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label=""
                >
                    {getFieldDecorator('username',{
                        rules:[{required:true,message:""}]
                    })(
                        <Input prefix={<Icon type='user'/>} placeholder=""/>
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label=""
                >
                    {getFieldDecorator('password', {
                        rules: [{
                            required: true, message: '',
                        }, {
                            validator: this.checkConfirm,
                        }],
                    })(
                        <Input prefix={<Icon type="lock"/>} type="password" placeholder=""/>
                    )}
                </FormItem>

                <FormItem
                    {...formItemLayout}
                    label=""
                >
                    {getFieldDecorator('confirm', {
                        rules: [{
                            required: true, message: '',
                        }, {
                            validator: this.checkPassword,
                        }],
                    })(
                        <Input prefix={<Icon type="lock"/>} type="password" onBlur={this.handleConfirmBlur} placeholder=""/>
                    )}
                </FormItem>
                <FormItem {...tailFormItemLayout}>
                    {getFieldDecorator('agreement', {
                        valuePropName: 'checked',
                    })(
                        <Checkbox><a href="javascript:;" onClick={this.showAgreement}></a></Checkbox>
                    )}
                </FormItem>
                <FormItem {...tailFormItemLayout}>
                    <Button type="primary" htmlType="submit" className="register-form-button"></Button>
                </FormItem>
            </Form>
            <Modal className="register-agreement-modal" visible={this.state.agreementVisible} onCancel={()=>{this.setState({agreementVisible:false})}} footer={null} title="">
                <h6></h6>
                <h6></h6>
                <h6></h6>
                <Button type="primary" onClick={()=>{this.setState({agreementVisible:false})}}></Button>
            </Modal>
        </div>
    }
}
export default Form.create()(Register);


